.formContainer {
    display: grid;
    grid-template-columns: 1fr;
    /* Single column layout */
    gap: 10px;
    /* Add spacing between form elements */
}

.inputField {
    width: 100%;
    /* Full width input fields */
    padding: 5px;
    /* Add padding for better readability */
}

.submitButton {
    background-color: #007bff;
    /* Custom button styling */
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
}